@import '../../styles/variables.sass'
    
.comics
    &__list
        margin-top: 45px

    &__grid
        display: grid
        grid-template-columns: repeat(auto-fill, 225px)
        justify-content: space-between
        row-gap: 55px

    &__item
        transition: 0.3s transform
        color: #fff
        background-color: $dark
        box-shadow: 5px 5px 10px rgba(0, 0, 0, .25)
        text-align: center
        &-img
            box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25)
            width: 225px
            height: 345px

        &-name
            margin-top: 10px
            font-weight: bold
            font-size: 14px
            line-height: 18px

        &-price
            margin-top: 5px
            font-weight: bold
            font-size: 14px
            line-height: 18px
            text-transform: uppercase

        &:hover
            box-shadow: 0 5px 20px #1922aa
            transform: translateY(-5px)
